        </div>
      </div>
      <div class="opuser_optime opcontent ">
        <div class="opuser content">
          
          <span v-if="$route.query.logcontent != ''">{{
            opContent
          }}</span>
          <div class="ql-snow" v-if="$route.query.logcontent == ''">
            <span></span>
            <div class="ql-editor" v-html="opContent"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
