<script>
// require styles
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
export default {
  created: function() {
    let that = this;
    console.log(this.$route.query);
    console.log(JSON.parse(this.$route.query.opContent));
    // console.log(JSON.parse(this.$route.query.opContent)[0]);
    that.opusers = this.$route.query.loguser;
    that.optime = this.$route.query.logtime;
    switch (this.$route.query.logcontent) {
      case "":
        that.opContent = "";
        break;
      case "":
        that.opContent =
          "" + JSON.parse(this.$route.query.opContent)[0].id;
        break;
      case "":
        that.opContent =
          "" + JSON.parse(this.$route.query.opContent)[0];
        break;
      case "":
        that.opContent = "";
        break;
      case "":
        that.opContent =
          "" + JSON.parse(this.$route.query.opContent)[0];
        break;
      case "":
        that.opContent =
          "" +
          JSON.parse(this.$route.query.opContent)[0].name +
          "" +
          JSON.parse(this.$route.query.opContent)[0].deviceNo +
          "";
        break;
      case "":
        that.opContent =
          "" +
          JSON.parse(this.$route.query.opContent)[0].name +
          "" +
          JSON.parse(this.$route.query.opContent)[0].deviceNo +
          "";
        break;
      case "":
        that.opContent = "ID" + this.$route.query.opContent;
        break;
      case "":
        that.opContent = "";
        break;
      case "":
        that.opContent = "" + this.$route.query.opContent;
        break;
      case "":
        that.opContent =
          "" +
          JSON.parse(this.$route.query.opContent)[0].name;
        break;
      case "":
        that.opContent =
          "" +
          JSON.parse(this.$route.query.opContent)[1].name;
        break;
      case "":
        that.opContent =
          "ID" + JSON.parse(this.$route.query.opContent)[0];
        break;
      case "":
        that.opContent = JSON.parse(this.$route.query.opContent)[0].record;
        break;
      case "":
        that.opContent =
          "ID" + JSON.parse(this.$route.query.opContent)[0];
        break;
      case "":
        that.opContent = "";
        break;
      case "":
        that.opContent =
          "" +
          JSON.parse(this.$route.query.opContent)[0].name;
        break;
      case "":
        that.opContent =
          "id" + JSON.parse(this.$route.query.opContent)[0];
        break;
      default:
        break;
    }
  },
  data() {
    return {
      opusers: "",
      optime: "",
      opContent: "",
    };
  },
  methods: {},
